import React, { useState } from "react"
import { Box, Button, Text } from 'grommet';
import styled from 'styled-components'


const Home = ({ buttons }) => {

  const [active, setActive] = useState(buttons && buttons[0])
  return <>
    <Box direction="row-responsive">
      {
        buttons && buttons.map((e, i) => <Box 
            key={i}
            onClick={() => setActive(e)}
            align="center" color="white"  
            background={active.label === e.label ? 'brand' : 'black'} 
            fill="horizontal" 
            pad="small">
            <Text color="white"> { e.label }</Text>
          </Box>)
      }
    </Box>
  </>
}

export default Home
